import { Component, OnInit, Input, Output, EventEmitter, SimpleChanges } from '@angular/core';

import { AccountsService } from 'src/app/api/controllers/Accounts';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-expense-post-payment',
  templateUrl: './expense-post-payment.component.html',
  styleUrls: ['./expense-post-payment.component.scss']
})
export class ExpensePostPaymentComponent implements OnInit {

  collection:any = {};

  instrumentTypeListData:any;
  paymentStatusListData:any;
  glAccountListData:any;

  isCollectionSubmitted:boolean = false;

  isError:boolean = false;
  alertMessage:string = "";

  @Input() totalAmountArray:any;
  @Input() invoiceIdArray:any;

  custInvoiceObjArray:any;

  minCollectionAmount:number = 1;
  maxCollectionAmount:any = 0;
  
  isMultipleEntry:boolean = false;

  @Output() collectionParams = new EventEmitter(false);

  constructor(
    private accountsService:AccountsService,
    private lookupService:LookupService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }


  submitExpensePostCollectionForm(form) {


      var custInvoiceObjArray = [];

        _.each(this.invoiceIdArray, (item, index) => {
            
          var details = {
              
              "apartmentId": parseInt(this.cookieService.get('apartmentId')),
              "paymentId": 11,
              "invoiceId": item,
              "amount": this.totalAmountArray[index],
              "comment": "",
              "isActive": true,
              "insertedBy": parseInt(this.cookieService.get('userId')),
              "insertedOn": "2020-01-10T06:59:54.422Z",
              "updatedBy": 0,
              "updatedOn": "2020-01-10T06:59:54.422Z"
          }

          custInvoiceObjArray.push(details);

        })

      this.isCollectionSubmitted = false;

    	let collectionDetails = {

        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "paymentAmount": parseInt(this.collection.paymentAmount),
        "instrumentTypeId": parseInt(this.collection.instrumentTypeId),
        "instrumentNumber": "",
        "payeeId": 1,
        "payeeName": "",
        "vendorPaymentAccountTypeId": 23,
        "comments": "",
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": "2019-12-15T19:36:14.09",
        "updatedBy": null,
        "updatedOn": null,
        "vendorPaymentInvoice": custInvoiceObjArray,
        "transReference1": "",
        "transReference2": "",
        "glAcctIndicator": "Income"

       }
       
    let vendorParams = {
      payment: collectionDetails
    }

    this.accountsService.addVendorPayment(vendorParams).subscribe((res:any) => {

      if(res.message){

           this.isCollectionSubmitted = true;
           this.sharedService.setAlertMessage("Payment done successfully");
           this.collectionParams.emit(true);
         }
         else {

            this.isCollectionSubmitted = true;
            this.isError = true;
            this.alertMessage = res.errorMessage;
         }

    },
    error => {
      this.isCollectionSubmitted = true;
    });


  }

  ngOnInit() {

  	this.collection = {};
  	this.collection.instrumentTypeId = "";

    let params = {
      LookupTypeId: 33
    }

  	//payment mode
    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {
          
       this.instrumentTypeListData = res.filter( item => {
          return item.isActive;
        });

    },
    error => {
      
    }
    );

    var accountParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      groupId: 3
    }

    this.accountsService.getGlAccountsByGroupId(accountParams).subscribe((res:any) => {

  		this.glAccountListData = res;

  	});

    this.isCollectionSubmitted = true;

    this.collection.paymentAmount = parseFloat(this.totalAmountArray[0]).toFixed(2)
    this.maxCollectionAmount = this.collection.paymentAmount

  }

  ngOnChanges(changes: SimpleChanges) {

    if(this.totalAmountArray.length > 1){
      
      this.isMultipleEntry = true;
      this.collection.paymentAmount = this.totalAmountArray.reduce((a, b) => {
        return a + b
      }).toFixed(2)

      this.maxCollectionAmount = this.collection.paymentAmount;

    }
    else {
      this.isMultipleEntry = false;
      this.collection.paymentAmount = parseFloat(this.totalAmountArray[0]).toFixed(2)
      this.maxCollectionAmount = this.collection.paymentAmount
    }


  }
 

}
