import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { VendorService } from 'src/app/api/controllers/Vendor';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-expense-pay-invoice',
  templateUrl: './expense-pay-invoice.component.html',
  styleUrls: ['./expense-pay-invoice.component.scss']
})
export class ExpensePayInvoiceComponent implements OnInit {

  isInvoiceDataLoaded:boolean = false;

  invoiceData:string = "";
  invoiceDataList:any;

  userDataList:any;
  glAccountListData:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  isInvoiceSelected:boolean = false;
  selectAllInvoices:boolean = false;

  totalAmountArray:any;
  selectedInvoiceIdArray:any;

  vendorDataList:any;
  isVendorDataLoaded:boolean = false;
  vendorCategoryData:any;

  constructor(
    private route: ActivatedRoute,
    private userService:UserService,
    private lookupService: LookupService,
  	private accountsService:AccountsService,
    private vendorService:VendorService,
  	private cookieService:CookieService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  getInvoiceDate(date){
  	return moment(date).format("DD/MM/YYYY");
  }

  getAccountName(account){

    var accountDetails = {
      glaccountId: 0
    };

    if(account == undefined || account.length == 0){
      accountDetails.glaccountId = 0;
    }
    else {
      accountDetails.glaccountId = account[0].glaccountId;
    }

  	var data = _.filter( this.glAccountListData, function( item ) {
      if(item.glaccountId === accountDetails.glaccountId)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].glaccountName;
    }

  	return '';
  }


  getCategoryName(id){
    
    var data = _.filter( this.vendorCategoryData, function( item ) {
      if(item.lookupValueId === parseInt(id))
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName;
    }

  }


  getAllInvoices(){

    this.totalAmountArray = [];
    this.selectedInvoiceIdArray = [];

    if(this.selectAllInvoices){
      _.each(this.invoiceDataList, (item)=>{
        item.checked = true;
        this.totalAmountArray.push(item.vendorInvoiceAmount)
        this.selectedInvoiceIdArray.push(item.vendorInvoiceId);
      })
      this.isInvoiceSelected = true;
    }
    else{
      _.each(this.invoiceDataList, (item)=>{
        item.checked = false;
      })
      this.isInvoiceSelected = false;
    }

  }

  getSelectedInvoice(invoice) {
    
    var length=0;

    this.totalAmountArray = [];
    this.selectedInvoiceIdArray = [];

    _.each(this.invoiceDataList, (item)=>{
        if(item.checked){
          length++;
          this.totalAmountArray.push(item.vendorInvoiceAmount);
          this.selectedInvoiceIdArray.push(item.vendorInvoiceId);
        }
     });

    if(length>0){
      this.isInvoiceSelected = true;
    }
    else{
      this.isInvoiceSelected = false;
      this.totalAmountArray = [];
      this.selectedInvoiceIdArray = [];
    }

    if(length==0){
      this.selectAllInvoices = false;
      this.totalAmountArray = [];
      this.selectedInvoiceIdArray = [];
    }

    if(length == this.invoiceDataList.length){
      this.selectAllInvoices = true;
    }


    
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }

  isCollectionMade(event){
    if(event)
      this.getCustInvoices();
  }



  getCustInvoices() {

    var params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      vendorId: this.route.params['value'].id
    }

    this.accountsService.getVendorInvoiceByVendorId(params).subscribe((res:any) => {


      this.invoiceDataList = res;


      this.totalItems = this.invoiceDataList.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.isInvoiceDataLoaded = true;
   

    })

  }

  ngOnInit() {

  this.getCustInvoices();

  let params = {
    apartmentId: parseInt(this.cookieService.get('apartmentId'))
  }

  this.vendorService.getVendorByApartmentId(params).subscribe((res:any) => {

      this.vendorDataList = res.filter(item => {
        return item.vendorId == this.route.params['value'].id;
      });

      this.isVendorDataLoaded = true;
      
      let categoryParams = {
        LookupTypeId: 57
      }

      this.lookupService.getLookupValueByLookupTypeId(categoryParams).subscribe((res:any) => {

        this.vendorCategoryData = res.filter(item => {
            return item.isActive;
        });

      });


  });

	var accountParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      groupId: 3
  }

  this.accountsService.getGlAccountsByGroupId(accountParams).subscribe((res:any) => {
    this.glAccountListData = res;
  });

  	

  }

}
